//
function validateProductName(productName) {
	var result = {};
	result.ok = false;
	if (!productName) {
		result.errorMsg = "";
		return result;
	} 
	if (productName.length > 20) {
		result.errorMsg = "20";
		return result;
	}
	result.ok = true;
	return result;
}

function validateProductQuantity(productQuantity) {
	var result = {};
	result.ok = false;
	if (!productQuantity) {
		result.errorMsg = "";
		return result;
	}
	if (productQuantity.length > 12) {
		result.errorMsg = "12";
		return result;
	}
	result.ok = true;
	return result;
}

function validateProductPrice(productPrice) {
	var result = {};
	result.ok = false;
	if (!productPrice || productPrice < 0) {
		result.errorMsg = "";
		return result;
	}
	result.ok = true;
	return result;
}

function validateCustomerName(customerName) {
	var result = {};
	result.ok = false;
	if (!customerName) {
		result.errorMsg = "";
		return result;
	} 
	if (customerName.length > 20) {
		result.errorMsg = "20";
		return result;
	}
	result.ok = true;
	return result;
}

function validateConsigneeName(consigneeName) {
	var result = {};
	result.ok = false;
	if (!consigneeName) {
		result.errorMsg = "";
		return result;
	} 
	if (consigneeName.length > 20) {
		result.errorMsg = "20";
		return result;
	}
	result.ok = true;
	return result;
}

function validateConsigneePhone(consigneePhone) {
	var result = {};
	result.ok = false;
	var phone_pattern = /^[0-9]{11}$/;
	if (!consigneePhone) {
		result.errorMsg = "";
		return result;
	} 
	if (!phone_pattern.test(consigneePhone)) {
		result.errorMsg = "";
		return result;
	}
	result.ok = true;
	return result;
}

function validateConsigneeAddress(regionId, detailAddress) {
	var result = {};
	result.ok = false;
	if (regionId === 0) {
		result.errorMsg = "";
		return result;
	}
	if (!detailAddress) {
		result.errorMsg = "";
		return result;
	}
	if (detailAddress.length > 50) {
		result.errorMsg = "50";
		return result;
	}
	result.ok = true;
	return result;
}

function validateInvoiceAmount(amount) {
	var result = {};
	result.ok = false;
	if (!amount || amount < 0) {
		result.errorMsg = "";
		return result;
	}
	result.ok = true;
	return result;
}

function validateInvoiceItem(name, data) {
	var result = {};
	result.ok = false;
	if (!data) {
		result.errorMsg = name + "";
		return result;
	} 
	if (data.length > 50) {
		result.errorMsg = name + "50";
		return result;
	}
	result.ok = true;
	return result;
}

function validateApplyInvoiceAmount(amount, maxValue) {
	var result = {};
	result.ok = false;
	if (amount === undefined || amount === null || amount === '') {
		result.errorMsg = "";
		return result;
	}
	if (amount <= 0 || amount > maxValue) {
		result.errorMsg = "" + (maxValue / 100) + "";
		return result;
	}
	result.ok = true;
	return result;
}

function validateExtraAmount(extraAmount, minValue) {
	var result = {};
	result.ok = false;
	if (extraAmount === undefined || extraAmount === null || extraAmount === '') {
		result.errorMsg = "";
		return result;
	}
	if (extraAmount <= minValue) {
		result.errorMsg = "" + (minValue / 100) + "";
		return result;
	}
	result.ok = true;
	return result;
}

function validateExtraCost(extraCost) {
	var result = {};
	result.ok = false;
	if (extraCost === undefined || extraCost === null || extraCost === '') {
		result.errorMsg = "";
		return result;
	}
	if (extraCost < 0) {
		result.errorMsg = "0";
		return result;
	}
	result.ok = true;
	return result;
}

function validateAfterSaleItem(name, data) {
	var result = {};
	result.ok = false;
	if (!data) {
		result.errorMsg = name + "";
		return result;
	} 
	if (data.length > 2000) {
		result.errorMsg = name + "2000";
		return result;
	}
	result.ok = true;
	return result;
}

function validateProductCost(cost, allowEmpty) {
	var result = {};
	result.ok = false;
	if (!cost) {
		if (allowEmpty) {
			result.ok = true;
			return result;
		} else {
			result.errorMsg = "0";
			return result;
		}
	}
	if (cost < 0) {
		result.errorMsg = "0";
		return result;
	}
	result.ok = true;
	return result;
}

function validateLogisticsCost(cost) {
	var result = {};
	result.ok = false;
	if (cost === undefined || cost === null || cost === '') {
		result.errorMsg = "";
		return result;
	}
	if (cost < 0) {
		result.errorMsg = "0";
		return result;
	}
	result.ok = true;
	return result;
}

function validateLogisticsItem(name, data) {
	var result = {};
	result.ok = false;
	if (!data) {
		result.errorMsg = name + "";
		return result;
	} 
	if (data.length > 2000) {
		result.errorMsg = name + "2000";
		return result;
	}
	result.ok = true;
	return result;
}

function validateRefundAmount(amount, maxValue) {
	var result = {};
	result.ok = false;
	if (!amount || amount < 0) {
		result.errorMsg = "";
		return result;
	}
	if (amount > maxValue) {
		result.errorMsg = "" + (maxValue / 100) + "";
		return result;
	}
	result.ok = true;
	return result;
}

function validateRefundAccount(account) {
	var result = {};
	result.ok = false;
	if (!account) {
		result.errorMsg = "";
		return result;
	} 
	if (account.length > 50) {
		result.errorMsg = "50";
		return result;
	}
	result.ok = true;
	return result;
}

function validateRefundReason(reason) {
	var result = {};
	result.ok = false;
	if (!reason) {
		result.errorMsg = "";
		return result;
	} 
	if (reason.length > 2000) {
		result.errorMsg = "2000";
		return result;
	}
	result.ok = true;
	return result;
}


//
function validatePaymentAmount(amount, maxValue) {
	var result = {};
	result.ok = false;
	if (!amount || amount < 0) {
		result.errorMsg = "";
		return result;
	}
	if (amount > maxValue) {
		result.errorMsg = "(" + (maxValue / 100) + ")";
		return result;
	}
	result.ok = true;
	return result;
}

//CRM
function validateSupplierName(supplierName) {
	var result = {};
	result.ok = false;
	if (!supplierName) {
		result.errorMsg = "";
		return result;
	} 
	if (supplierName.length > 20) {
		result.errorMsg = "20";
		return result;
	}
	result.ok = true;
	return result;
}

function validateDisplayName(displayName) {
	var result = {};
	result.ok = false;
	if (!displayName) {
		result.errorMsg = "";
		return result;
	}
	if (displayName.length > 20) {
		result.errorMsg = "20";
		return result;
	}
	result.ok = true;
	return result;
}

function validateUserName(username) {
	var result = {};
	result.ok = false;
	if (!username) {
		result.errorMsg = "";
		return result;
	}
	if (username.length > 50) {
		result.errorMsg = "50";
		return result;
	}
	result.ok = true;
	return result;
}

function validatePassword(password) {
	var result = {};
	result.ok = false;
	var password_pattern = /^[a-zA-Z0-9]*$/;
	if (!password) {
		result.errorMsg = "";
		return result;
	}
	if (password.length > 20) {
		result.errorMsg = "20";
		return result;
	}
	if (!password_pattern.test(password)) {
		result.errorMsg = "";
		return result;
	}
	result.ok = true;
	return result;
}

const DEFAULT_PASSWORD = '******';

export var orderValidator = {
	validateProductName,
	validateProductQuantity,
	validateProductPrice,
	validateCustomerName,
	validateConsigneeName,
	validateConsigneePhone,
	validateConsigneeAddress,
	validateInvoiceAmount,
	validateInvoiceItem,
	validateApplyInvoiceAmount,
	validateExtraAmount,
	validateExtraCost,
	validateAfterSaleItem,
	validateProductCost,
	validateLogisticsCost,
	validateLogisticsItem,
	validateRefundAmount,
	validateRefundAccount,
	validateRefundReason,
};

export var financeValidator = {
	validatePaymentAmount,
};

export var crmValidator = {
	validateSupplierName,
	validateDisplayName,
	validateUserName,
	validatePassword,
	DEFAULT_PASSWORD,
};




