<!-- payQrCodeDialog.vue -->
<template>
    <div class="modal" v-show="show">
        <div class="modal-dialog" style="width:320px;">
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" @click="closeDialog">&times;</button>
                    <h4 class="modal-title">{{title}}</h4>
                </div>
                <div class="modal-body">
                	<h4><span style="font-size:20px;color:#f54404">{{data.amountToPay | realPrice}}</span></h4>
                	<br>
                	<div id="qrcode_pay">
                	</div>
                	<br>
                	<p class="text-danger">{{description}}</p>
                	<p></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'payQrCodeDialogView',
    data() {
        return {
        }
    },
    computed: {
        title() {
            if (this.data.type === 'WECHAT_QR') {
                return '';
            } else {
            	return '';
            }
        },
        description() {
        	if (this.data.type === 'WECHAT_QR') {
                return '';
            } else {
            	return '';
            }
        },
    },
    props: ['show', 'data'],
    watch: {
    	show(val, oldVal) {
            if (val) {
            	$("#qrcode_pay").html('').qrcode({
                    width: 280,
                    height: 280,
                    text: this.data.qrCodeUrl,
                });
            }
        },
    },
    methods: {
    	closeDialog() {
            this.show = false;
        },
    },
}

</script>

<style scoped>
.modal {
    height: 100%;
}

.modal-dialog {
    top: 15%;
}
</style>


